<template>
	<view>
		<view style="width: 526rpx; margin: 120rpx auto 0rpx;">
			<view style=" margin-bottom:76rpx;">
				<text style="font-size: 46rpx;font-weight: 500;">欢迎登陆</text>
			</view>
			<view>
				<u-form :error-type="errorType" :model="form" ref="uForm">
					<u-form-item label="" prop="userNamePhone">
						<u-input v-model="form.userNamePhone" height="50" placeholder="请输入手机号" />
					</u-form-item>
					<u-form-item label="" prop="userPwdPhone">
						<u-input type="password" v-model="form.userPwdPhone" height="50" placeholder="请输入手机密码" />
					</u-form-item>
				</u-form>
			</view>
		<!-- 	<view v-else>
				<u-form :error-type="errorType" :model="form" ref="uForm">
					<u-form-item prop="userName">
						<u-input v-model="form.userName" height="50" placeholder="请输入账号" />
					</u-form-item>
					<u-form-item prop="userPwd">
						<u-input v-model="form.userPwd" height="50" placeholder="请输入账号密码" />
					</u-form-item>
				</u-form>
			</view> -->
			<view class="flex justify-end mt-2">
				<!-- 手机登录 -->
				<view @click="handleSwitchLanding">
					<text class="font-sm" style="color: #909399;">手机登录</text>
				</view>
				<!-- 点击注册 -->
				<view class="ml-2" @click="handleRegister">
					<text class="font-sm" style="color: #1a1da0;">点我注册</text>
				</view>
				<!-- 遇到问题 -->
				<view class="ml-2">
					<text class="font-sm" style="color: #909399;">遇到问题</text>
				</view>
			</view>
		</view>
		<!-- 登陆按钮 -->
		<u-button type="success" :custom-style="customStyle" @click="handleBtnClick">登陆</u-button>
		<!-- 其他登陆方式 -->
		<view class="flex align-center justify-center" style="padding: 306rpx 130rpx 130rpx 130rpx;">
			<view class="flex align-center justify-between" style="width: 400rpx;">
				<view class="flex align-center flex-column ml-3">
					<u-icon name="weixin-fill" size="60" color="#53c240"></u-icon>
					<text>微信</text>
				</view>
				<view class="flex align-center flex-column mr-3">
					<u-icon name="qq-fill" size="60" color="#11b7e9"></u-icon>
					<text>QQ</text>
				</view>
			</view>
		</view>
		<!-- 用户服务协议 -->
		<view style="color: #909399;padding: 16rpx 34rpx;">
			登陆代表同意
			<text style="color: #f90;">用户协议、隐私政策，</text>
			并授权使用您的账号信息（如昵称、头像、收获地址）以便您统一管理
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				errorType: ['toast'],
				form: {
					// 账号
					userName: '',
					// 密码
					userPwd: '',
					userNamePhone: '',
					userPwdPhone: ''
				},
				// 登陆方式
				// switchLanding: true,
				// 登陆按钮样式
				customStyle: {
					marginTop: '40rpx',
					width: "526rpx"
				},
				rules: {
					userNamePhone: [{
							required: true,
							message: '请输入手机号',
							trigger: ['change', 'blur']
						},
						{
							validator: (rule, value, callback) => {
								return this.$u.test.mobile(value);
							},
							message: '请输入合法的手机号',
							trigger: ['change', 'blur'],
						}
					],
					userPwdPhone: [{
						required: true,
						message: '请输入手机密码',
						trigger: ['change', 'blur']
					}]
				}
			}
		},
		onLoad() {
			uni.getStorage({
				key:'user_info',
				success:(res => {
					return this.form.userNamePhone = res.data.P_Telephone
				})
			})
		},
		methods: {
			// 切换登陆方式
			handleSwitchLanding() {
				this.switchLanding = !this.switchLanding
			},
			// 登录按钮
			handleBtnClick() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						this.$u.post('/LoginInfo', {
							account: this.form.userNamePhone,
							password: this.form.userPwdPhone,
							type:'2'
						}).then(res => {
							uni.setStorage({
								key:'user_login',
								data:res.data
							})
							if(res.code !==200){
								uni.showToast({
									title:res.info,
									icon:'none'
								})
								return false;
							}else{
								this.$u.route({
									url:'/pages/tabbar/index/index',
									type:'tab'
								})
							}
						})
					}
				})
			},
			// 点击跳转注册页面
			handleRegister(){
				this.$u.route('/pages/register/register')
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		}
	}
</script>

<style>
</style>